#jobs{max-width:800px;margin:0 auto}.react-tabs{display:flex;color:#fefefe;transition:all .25s cubic-bezier(0.645, 0.045, 0.355, 1)}.react-tabs__tab-list{display:flex;flex-direction:column;width:200px;margin:0;padding:0;color:#fefefe;background:rgba(0,0,0,0)}.react-tabs__tab{display:flex;align-items:center;height:40px;list-style:none;padding:12px 6px;cursor:pointer;color:#fefefe;font-family:"Fira Mono",monospace;font-size:16px}.react-tabs__tab--selected{background:#112240;border-color:#1c1d1f;border-left:4px solid #64ffda;color:#64ffda}.react-tabs__tab-panel{display:none;width:600px;padding:20px}.react-tabs__tab-panel--selected{display:block}.react-tabs__tab{padding-left:24px}.react-tabs__tab--selected{padding-left:21px;transition:all .25s cubic-bezier(0.645, 0.045, 0.355, 1)}.panel-content{transition:all .25s cubic-bezier(0.645, 0.045, 0.355, 1);min-height:max-content}.panel-content h2{font-weight:400;font-size:22px}.panel-content p{font-family:"Fira Mono",monospace;font-size:13px;color:#ccd6f6;margin-bottom:25px}.panel-content ul{list-style:none;font-size:16px;color:#ccd6f6}.panel-content ul li{position:relative;padding-left:30px;margin-bottom:10px}.panel-content ul li::before{content:"▹";position:absolute;left:0px;color:#64ffda}@media only screen and (max-width: 762px){.react-tabs{display:block}.react-tabs__tab{padding:5px 15px}.react-tabs__tab-list{flex-direction:initial;width:100%;overflow-x:scroll;height:auto;padding:2px 0}.react-tabs__tab--selected{border-left:initial;border-bottom:4px solid #64ffda}.react-tabs__tab-panel{width:100%;padding:20px 0 20px 20px}}