@import "../../Vars.scss";

#projects ul,
ol {
  list-style: none;
}

#projects {
  ul > li {
    position: relative;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(12, 1fr);
    -webkit-box-align: center;
    align-items: center;
    margin-bottom: 100px;
    max-height: 500px;

    &:nth-of-type(2n + 1) {
      .prj-content {
        grid-column: 7 / -1;
        text-align: right;
      }
      .prj-overline {
        text-align: right;
      }
      .prj-techlist {
        text-align: right;
      }
    }
    .prj {
      &-content {
        position: relative;
        grid-area: 1 / 1 / -1 / 7;
        z-index: 2;
      }

      &-overline {
        font-size: 14px;
        font-weight: 300;
        font-family: $font-mono;
        color: $green;
      }
      &-title {
        font-size: 22px;
        color: $white;
        padding-bottom: 20px;
      }
      &-desc {
        background-color: $light-blue;
        padding: 20px;
        font-size: 15px;
      }
      &-techlist li {
        display: inline;
        font-size: 14px;
        font-weight: 300;
        font-family: $font-mono;
        margin: 0 5px;
      }
      &-links svg {
        margin: 10px 5px;
        width: 25px;
        stroke: $slate;
        transition: $transition;
        &:hover {
          stroke: $green;
        }
      }
    }

    &:nth-of-type(2n + 1) .prj-img {
      grid-column: 1 / 8;
    }
    .prj-img {
      grid-area: 1 / 6 / -1 / -1;
      position: relative;
      z-index: 1;
    }
    .prj-img {
      max-width: 700px;
      max-height: 350px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

@media only screen and (max-width: 762px) {
  .prj-img {
    img {
      filter: blur(5px);
    }
    .overlay {
      background-color: $blue !important;
      mix-blend-mode: repeat !important;
      filter: blur(5px);
      &:hover {
        opacity: 1 !important;
      }
    }
  }

  #projects ul > li {
    &:nth-of-type(2n + 1) .prj-img {
      grid-column: 1/-1;
    }
    .prj-img {
      grid-column: 1/-1;
    }
    &:nth-of-type(2n + 1) .prj-content {
      padding: 20px;
      grid-column: 1 / -1;
    }
    .prj-content {
      padding: 20px;
      grid-column: 1 / -1;
    }
    .prj-desc {
      //   background-color: $green;
      //   opacity: 0.5;
    }
  }
}
